<script>
import { mapState, mapActions } from "vuex";

export default {
  name: "heading",
  computed: {
    ...mapState({
      user: state => state.auth.user,
      app: state => state.auth.application
    })
  },
  methods: {
    ...mapActions({})
  }
};
</script>

<template>
  <div class="component heading">
    <div class="content">
      <div class="main-search">
        <input class="main-search" name="q" type="text" placeholder="Pesquisar" autocomplete="off">
        <!-- <button class="btn btn-sm btn-primary" v-if="app">{{app.name}}</button> -->
      </div>
    </div>
    <div v-if="user" class="menubar noselect">
      <router-link class="item" style="margin-left:10px;" to="/login">Sair</router-link>
      <div class="item">
        <i class="fas fa-bell" style="font-size: 18px;"></i>
      </div>
      <div class="item">{{user.name}}</div>
    </div>
  </div>
</template>

<style lang="scss">
.component.heading {
  display: flex;
  height: 50px;
  background: #fff;
  flex-shrink: 0;

  .content {
    display: flex;
    flex-grow: 1;
    flex-direction: row;
    align-items: center;

    .main-search {
      input {
        width: 420px;
        margin-left: 30px;
        height: 32px;
        background: rgb(246, 246, 246);
        border: 1px solid #d8d9dc;
        border-radius: 2px;
        padding-left: 10px;
      }
      button.btn {
        margin-left: 10px;
      }
    }
  }

  .menubar {
    display: flex;
    flex-grow: 0;
    flex-direction: row;
    margin-right: 20px;

    .item {
      display: flex;
      align-items: center;
      padding: 10px;
      cursor: pointer;

      &:hover {
        background: #f3f3f3;
      }
    }
  }
}
</style>



