<script>
import $ from "jquery";

export default {
  props: {
    params: {
      type: Object,
      default() {
        return {
          selected: null
        };
      }
    }
  },
  data() {
    return {};
  },
  created() {
    if (this.params.data == undefined) {
      this.params.data = {};
    }
    this.params.countries = {
      br: {
        label: "Brasil"
      }
    };

    this.params.states = {
      ac: { label: "Acre" },
      al: { label: "Alagoas" },
      ap: { label: "Amapá" },
      am: { label: "Amazonas" },
      ba: { label: "Bahia" },
      ce: { label: "Ceará" },
      df: { label: "Distrito Federal" },
      es: { label: "Espírito Santo" },
      go: { label: "Goiás" },
      ma: { label: "Maranhão" },
      mt: { label: "Mato Grosso" },
      ms: { label: "Mato Grosso do Sul" },
      mg: { label: "Minas Gerais" },
      pa: { label: "Pará" },
      pb: { label: "Paraíba" },
      pr: { label: "Paraná" },
      pe: { label: "Pernambuco" },
      pi: { label: "Piauí" },
      rj: { label: "Rio de Janeiro" },
      rn: { label: "Rio Grande do Norte" },
      rs: { label: "Rio Grande do Sul" },
      ro: { label: "Rondônia" },
      rr: { label: "Roraima" },
      sc: { label: "Santa Catarina" },
      sp: { label: "São Paulo" },
      se: { label: "Sergipe" },
      to: { label: "Tocantins" }
    };
  },
  mounted() {
    $(this.$el).find('.form-states').focus()
  },
  computed: {
    country: {
      get() {
        return this.params.data.country.acronym;
      },
      set(key) {
        this.params.data.country.acronym = key;
      }
    },
    state: {
      get() {
        return this.params.data.state ? this.params.data.state.acronym : null;
      },
      set(key) {
        this.params.data.state.acronym = key;
      }
    },
    city: {
      get() {
        return this.params.data.city ? this.params.data.city.label : null;
      },
      set(key) {
        this.params.data.city.label = key;
      }
    },
    district: {
      get() {
        return this.params.data.district
          ? this.params.data.district.label
          : null;
      },
      set(key) {
        this.params.data.district.label = key;
      }
    },
    address: {
      get() {
        return this.params.data.address ? this.params.data.address : null;
      },
      set(key) {
        this.params.data.address = key;
      }
    },
    observation: {
      get() {
        return this.params.data.observation
          ? this.params.data.observation
          : null;
      },
      set(key) {
        this.params.data.observation = key;
      }
    }
  }
};
</script>

<template>
  <div class="attr-module type-options">
    <div class="form-group">
      <span>País</span>
      <select class="form-control form-countries" v-model="country">
        <option v-for="(row, key) in params.countries" :key="key" :value="key">{{row.label}}</option>
      </select>
    </div>
    <div class="form-group">
      <span>Estado</span>
      <select class="form-control form-states" v-model="state">
        <option v-for="(row, key) in params.states" :key="key" :value="key">{{row.label}}</option>
      </select>
    </div>
    <div class="form-group">
      <span>Cidade</span>
      <input class="form-control form-city" v-model="city">
    </div>
    <div class="form-group">
      <span>Bairro</span>
      <input class="form-control form-district" v-model="district">
    </div>
    <div class="form-group">
      <span>Endereço</span>
      <input class="form-control form-address" v-model="address">
    </div>
    <div class="form-group">
      <span>Observações</span>
      <input class="form-control form-observation" v-model="observation">
    </div>
  </div>
</template>


