import Vue from 'vue';

import _ from 'lodash'
import { type } from 'os';

export default function () {

	var $vm = new Vue({
		data() {
			return {
				pattern: {},
				current: {},
				mixed: {},
				new: {},
			}
		},
		watch: {
			pattern(pattern) {
				this.mix();
				this.$emit('pattern-change', pattern);
			},
			current(current) {
				this.mix();
				this.$emit('current-change', current);
			},
			mixed: {
				handler(mixed) {
					this.$emit('mixed-change', mixed);
				},
				deep: true
			},
			new(_new) {
				this.$emit('new-change', _new);
			},
		},
		methods: {

			setData(pattern, current) {
				this.pattern = pattern;
				this.current = current;
			},

			setPatternData(pattern) {
				this.pattern = pattern;
			},

			setCurrentData(current) {
				this.current = current;
			},

			mix() {
				this.mixed = this.getMixedData();
			},

			submit() {
				// $oldMix = this.getMixedData();
				this.new = this.getObjectDiff(this.pattern, this.mixed);
			},

			getData() {
				return this.getObjectDiff(this.pattern, this.mixed);
			},

			getMixedData() {
				if (this.pattern) {
					return _.merge({}, this.pattern, this.current);
				}
			},

			getObjectDiff(pattern, data) {

				function changes(pattern, data) {

					if (typeof pattern != 'object' || typeof data != 'object') { return; }

					if (pattern.constructor.name != data.constructor.name) { return; }

					// -------------------------------------------------------

					// OBJECT ou ARRAY

					if (pattern.constructor.name == 'Object') {

						var diff = {}

						for (var key in data) {

							if (!pattern.hasOwnProperty(key)) {
								diff[key] = data[key];
								continue;
							}

							if (pattern[key] === data[key]) {
								continue;
							}

							if (typeof pattern[key] == 'object' && pattern[key].constructor.name == data[key].constructor.name) {
								var c = changes(pattern[key], data[key]);
								if (c) {
									diff[key] = c;
								}
								continue;
							}

							diff[key] = data[key];
						}

						return !_.isEmpty(diff) ? diff : undefined;
					}

					if (pattern.constructor.name == 'Array') {

						var diff = []

						for (var i in data) {

							if (pattern[i] == undefined) {
								diff[i] = data[i];
								continue;
							}

							if (pattern[i] === data[i]) {
								continue;
							}

							if (typeof pattern[i] == 'object' && pattern[i].constructor.name == data[i].constructor.name) {
								var c = changes(pattern[i], data[i]);
								if (c) {
									diff[i] = c;
								}
								continue;
							}

							diff[i] = data[i];
						}

						return diff.length > 0 ? diff : null;
					}
				}

				var c = changes(JSON.parse(JSON.stringify(pattern)), JSON.parse(JSON.stringify(data)));

				if (c) { return c; }

				return pattern.constructor.name == 'Object' ? {} : [];
			}

			// getObjectDiff(obj1, obj2) {

			// 	var response = {}

			// 	for (var key in obj2) {

			// 		if (obj1[key])
			// 	}

			// 	return response;
			// }
		}
	});

	return $vm;
}


